.card {
  display:block;
  margin-top:25px;
  margin-left:auto;
  margin-right:auto;
  background: #fff;
  border-radius: 2px;
  /*margin: 1rem;*/
  /*position: relative; */
  width: 500px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 60%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}



